import {normalizeStringForSearch} from './string';

type TMatchFilter = (
  text: string,
  item: Record<string, any>,
  queryKeys: string[],
  uniqueKeys: string[],
) => 'isUnique' | 'isMatch' | 'false';

export const matchFilter: TMatchFilter = (
  text: string,
  item: Record<string, any>,
  queryKeys: string[],
  uniqueKeys: string[] = [],
) => {
  let result = false;
  for (let uKey of uniqueKeys) {
    result = result || `${item?.[uKey]}` === text;
  }
  if (result) return 'isUnique';
  for (let key of queryKeys) {
    result =
      result ||
      normalizeStringForSearch(`${item?.[key]}`).includes(
        normalizeStringForSearch(text),
      );
  }
  if (result) return 'isMatch';
  return 'false';
};
