import {Keyboard} from 'react-native';
import create from 'zustand';

interface IKeyboardStore {
  isShown: boolean;
  height: number;
  setShow: (value: boolean) => void;
  setHeight: (value: number) => void;
}

class SKeyboardService {
  private readonly _store;
  constructor() {
    this._store = create<IKeyboardStore>(set => ({
      isShown: false,
      height: 0,
      setShow: value =>
        set(prev => ({
          ...prev,
          isShown: value,
        })),
      setHeight: value =>
        set(prev => ({
          ...prev,
          height: value,
        })),
    }));
  }

  get isShown() {
    return this._store.getState().isShown;
  }

  get setShow() {
    return this._store.getState().setShow;
  }

  dismiss = () => {
    if (this.isShown) Keyboard.dismiss();
  };

  get height() {
    return this._store.getState().height;
  }

  get setHeight() {
    return this._store.getState().setHeight;
  }
}

export const KeyboardService =  new SKeyboardService();
