import AsyncStorage from "@react-native-async-storage/async-storage";

const KEY_NAME = "keyboard";

class _MaxKeyboard {
  async getHeight() {
    return AsyncStorage.getItem(KEY_NAME).then((res) => Number(res) || 0);
  }

  async setHeight(value: number) {
    await AsyncStorage.setItem(KEY_NAME, value.toString());
  }
}

export const MaxKeyboard = new _MaxKeyboard();
