import { batch } from "react-redux";
import {
  setCommentLength,
  setCommentsQuery,
  setPostsQuery,
  syncComments,
  syncPosts,
} from "./index";
import { IComment } from "./type";
import { Core, Fetch, FileType } from "../../services";

const getDataWhenError = <T>(data: T) => {
  return JSON.parse(
    '{"code"' + (data as unknown as string).split('{"code"')[1]
  ) as unknown as T;
};

export interface IRequestLoad {
  hid: string;
  token: string;
  last_id?: string;
  method?: "prev";
  position?: string;
}

export const requestLoadComment = async (
  params: IRequestLoad,
  appName: string,
  type: "post" | "comment"
) => {
  const { data } = await Fetch.postWithToken<{
    comments?: IComment[];
    posts?: IComment[];
  }>(`@${Core.systemPath}/${appName}/ajax/api/${type}/load`, params);

  batch(() => {
    if (data.comments) setCommentLength(data.comments.length);
    if (data.comments?.length) {
      syncComments(data.comments);

      const newComments =
        data.comments?.map((item: IComment) => item.hid).reverse() || [];

      setCommentsQuery((prev) => ({
        ...prev,
        [params.hid]:
          params.method == "prev" && params.position !== undefined
            ? [...newComments, ...prev[params.hid]]
            : newComments,
      }));
    }
    if (data.posts?.length) {
      syncPosts(data.posts);

      const newPosts = data.posts?.map((item: IComment) => item.hid) || [];

      setPostsQuery((prev) => ({
        ...prev,
        [params.hid]:
          params.last_id !== undefined
            ? [...prev[params.hid], ...newPosts]
            : newPosts,
      }));
    }
  });
  return type === "comment" ? data.comments : data.posts;
};

interface IRequestPost {
  hid: string;
  token: string;
  comment: string;
  content: string;
  file?: FileType[] | FileType;
}

export const requestPostComment = async (
  params: IRequestPost,
  appName: string,
  type: "post" | "comment"
) => {
  const { data } = await Fetch.postWithToken<{
    comment?: IComment;
    post?: IComment;
  }>(`@${Core.systemPath}/${appName}/ajax/api/${type}/post`, params);

  // const data = getDataWhenError(_data);

  batch(() => {
    if (data.comment) {
      syncComments([data.comment]);
      setCommentsQuery((prev) => ({
        ...prev,
        [params.hid]: [...(prev?.[params.hid] || []), data.comment?.hid || ""],
      }));
    }
    if (data.post) {
      syncPosts([data.post]);
      setPostsQuery((prev) => ({
        ...prev,
        [params.hid]: [data.post?.hid || "", ...(prev?.[params.hid] || [])],
      }));
    }
  });
  return type === "comment" ? data.comment : data.post;
};

export const requestReaction =
  (sync: (params: any[]) => any, appName: string) =>
  async ({
    hid,
    token,
    reaction,
  }: {
    hid: string;
    token: string;
    reaction: string;
  }) => {
    const { data } = await Fetch.postWithToken<{
      origin: {
        reactions: { reaction: string; user_id: string }[];
      } & object;
    }>(`@${Core.systemPath}/${appName}/ajax/api/like/reaction`, {
      hid,
      token,
      reaction,
    });

    // const data = getDataWhenError(_data);

    batch(() => {
      if (data.origin) {
        sync([data.origin]);
      }
    });
    return data?.origin;
  };
