import {useCallback, useEffect, useMemo, useState, useRef} from 'react';
import {
  EmitterSubscription,
  Keyboard,
  KeyboardEventListener,
  Platform,
} from 'react-native';
import {KeyboardService} from '../services';

export const useKeyboard = () => {
  const isShown = useRef(false);
  const keyboardHeight = useRef(0);
  const [update, forceUpdate] = useState(false);

  const onKeyboardShowHandler: KeyboardEventListener = useCallback(e => {
    isShown.current = true;
    keyboardHeight.current = e.endCoordinates.height - (Platform.OS == 'android' ? 13 : 0);
    forceUpdate(p=>!p);
    KeyboardService.setHeight(
      e.endCoordinates.height - (Platform.OS == 'android' ? 13 : 0),
    );
    KeyboardService.setShow(true);
  }, []);

  const onKeyboardHideHandler = useCallback(() => {
    isShown.current = false;
    keyboardHeight.current = 0;
    forceUpdate(p=>!p);
    KeyboardService.setHeight(0);
    KeyboardService.setShow(false);
  }, []);

  useEffect(() => {
    let keyboardWillShow: EmitterSubscription;
    let keyboardDidShow: EmitterSubscription;
    let keyboardWillHide: EmitterSubscription;
    let keyboardDidHide: EmitterSubscription;

    if (Platform.OS === 'android') {
      keyboardDidShow = Keyboard.addListener(
        'keyboardDidShow',
        onKeyboardShowHandler,
      );
      keyboardDidHide = Keyboard.addListener(
        'keyboardDidHide',
        onKeyboardHideHandler,
      );
    } else {
      keyboardWillShow = Keyboard.addListener(
        'keyboardWillShow',
        onKeyboardShowHandler,
      );
      keyboardWillHide = Keyboard.addListener(
        'keyboardWillHide',
        onKeyboardHideHandler,
      );
    }

    return () => {
      if (Platform.OS === 'android') {
        keyboardDidShow.remove();
        keyboardDidHide.remove();
      } else {
        keyboardWillShow.remove();
        keyboardWillHide.remove();
      }
    };
  }, []);

  return useMemo(() => ({
    isShown: isShown.current,
    keyboardHeight: keyboardHeight.current
  }), [update]);
};
