import create from "zustand";
import { persist } from "zustand/middleware";
import AsyncStorage from "@react-native-async-storage/async-storage";
import _styled, {
  useTheme as _useTheme,
  ReactNativeStyledInterface,
} from "styled-components/native";
import type { _DefaultTheme } from "./types";

export enum ETheme {
  light = "light",
  dark = "dark",
}

interface IStore {
  themeName: ETheme;
}

export const useThemeStore = create(
  persist<IStore>(
    (set) => ({
      themeName: ETheme.light,
    }),
    {
      name: "theme-storage",
      getStorage: () => AsyncStorage,
    }
  )
);

export const setThemeName = (value: ETheme) =>
  useThemeStore.setState({ themeName: value });

export const getThemeName = () => useThemeStore.getState().themeName;

export { ThemeProvider, ThemeContext } from "styled-components/native";
export type DefaultTheme = _DefaultTheme;

export const useTheme: () => DefaultTheme =
  _useTheme as unknown as () => DefaultTheme;
export const styled =
  _styled as unknown as ReactNativeStyledInterface<DefaultTheme>;
