import {LayoutStyles, TextStyles} from "../constants";

interface AnyProps {
    [x: string]: any;
}

class CModifier {
    extractThingsFromProps = (props: AnyProps, things: AnyProps) => {
        let output = '';
        for (const propsKey of Object.keys(props).reverse()) {
            const value = things[propsKey];
            if (value) output += things[propsKey];
        }

        return output;
    };

    extractTextStylesFromProps = (props: AnyProps) => this.extractThingsFromProps(props, TextStyles);
    extractLayoutStylesFromProps = (props: AnyProps) => this.extractThingsFromProps(props, LayoutStyles);
}

export const Modifier = new CModifier();
