import { LogBox } from "react-native";

export const ignoreWarnings = (ignoreWarns: string[]) => {
  if (__DEV__) {
    const warn = console.warn;
    console.warn = (...arg) => {
      for (const warning of ignoreWarns) {
        if (arg[0].startsWith(warning)) {
          return;
        }
      }
      warn(...arg);
    };

    LogBox.ignoreLogs(ignoreWarns);
  }
};
