import { createNavigate } from "../global";
import { Core } from "../services";
import { Linking } from "react-native";

const navigateWebView = (uri: string) => {
  createNavigate("WebViewScreen")({ uri });
};

const pushWebView = (uri: string) => {
  createNavigate("WebViewScreen")({ uri });
};

const getBaseLink = (link: string) => {
  const path = Core.systemPath;
  const urlType = getUrlType(link);
  if (urlType == URLType.TRUE && !link.startsWith(Core.urlScheme + "-")) {
    let app =
      link.replace(`https://${path}.${Core.sysUrl}/`, "").split("/")?.[0] || "";
    return link.replace(
      `https://${path}.${Core.sysUrl}/${app}`,
      `${Core.urlScheme}-${app}:/`
    );
  }
  return link;
};

export const openURL = async (uri: string) => {
  const urlType = getUrlType(uri);

  if (urlType === URLType.TRUE) {
    //TODO: Navigate inside app or in-app browser
    try {
      const baseLink = getBaseLink(uri);
      let supported;
      try {
        supported = await Linking.canOpenURL(baseLink);
      } catch (e) {}
      if (supported) {
        await Linking.openURL(baseLink);
      } else {
        pushWebView(baseLink);
      }
      return true;
    } catch (error) {}
  } else if (urlType === URLType.IN_APP) {
    try {
      navigateWebView(uri);
      return true;
    } catch (e) {}
  } else if (urlType === URLType.OUTSIDE) {
    try {
      await Linking.openURL(uri);
      return true;
    } catch (error) {}
  }
};

const getUrlType = (url: string): URLType => {
  // noinspection SuspiciousTypeOfGuard
  if (typeof url !== "string") {
    return URLType.INVALID;
  }

  if (url === "about:blank") {
    return URLType.BLANK;
  }

  const regex = new RegExp(
    "https?:\\/\\/(.+?.)?" + Core.sysUrl.split(".").join("\\."),
    "gi"
  );

  const meRegex = new RegExp(
    "https?:\\/\\/(.+?.)?" + "me." + Core.sysUrl.split(".").join("\\."),
    "gi"
  );
  if (url.match(meRegex)) {
    return URLType.IN_APP;
  }

  if (url.startsWith(Core.urlScheme + "-") || url.match(regex)) {
    return URLType.TRUE;
  }

  return URLType.OUTSIDE;
};

enum URLType {
  INVALID = "invalid",
  BLANK = "blank",
  TRUE = "true",
  IN_APP = "in-app",
  OUTSIDE = "outside",
}
