import { setStore } from "./getStore";
import { meReducer, resetMe, setMeStore } from "./me";
import { resetSystem, setSystemStore, systemReducer } from "./system";
import {
  resetUserGroup,
  setUserGroupStore,
  userGroupReducer,
} from "./userGroups";
import { resetUser, setUserStore, userReducer } from "./users";
import AsyncStorage from "@react-native-async-storage/async-storage";
import { batch } from "react-redux";
import {
  Action,
  AnyAction,
  applyMiddleware,
  combineReducers,
  createStore,
  ReducersMapObject,
  Store,
} from "redux";
import { composeWithDevTools } from "redux-devtools-extension";
import { persistReducer, persistStore } from "redux-persist";
import {
  commentsReducer,
  postsReducer,
  setCommentsStore,
  setPostsStore,
} from "./comments";
import { giphyReducer, setGiphyStore } from "./giphy";

export * from "./auth";
export * from "./me";
export * from "./users";
export * from "./system";
export * from "./userGroups";
export * from "./comments";
export * from "./giphy";

let _reducers: ReducersMapObject = {};
let _whitelist: string[] = [];

const middlewares: never[] = [];
if (__DEV__) {
  const createDebugger = require("redux-flipper").default;
  // @ts-ignore
  middlewares.push(createDebugger());
}
const enhancer = composeWithDevTools(applyMiddleware(...middlewares));

const initReducers = {
  users: userReducer,
  me: meReducer,
  system: systemReducer,
  userGroups: userGroupReducer,
  comments: commentsReducer,
  posts: postsReducer,
  giphy: giphyReducer,
};

const appReducer = combineReducers(initReducers);

export const store = createStore(appReducer, enhancer);

/* -- set store -- */
setStore(store);
setUserStore(store);
setMeStore(store);
setSystemStore(store);
setUserGroupStore(store);
setCommentsStore(store);
setPostsStore(store);
setGiphyStore(store);

/* -- setup store -- */
export const setupStore = <S, A extends Action = AnyAction>(
  reducers?: ReducersMapObject<S, A>,
  setStoreFn?: (store: Store<any, AnyAction>) => void,
  whitelist?: string[]
) => {
  /* -- add reducers -- */
  _reducers = { ..._reducers, ...reducers };

  /* -- add whitelist -- */
  if (whitelist) _whitelist = [...new Set([..._whitelist, ...whitelist])];

  /* -- combine reducers -- */
  const appReducer = combineReducers({
    ...initReducers,
    ..._reducers,
  });

  const rootReducer = (state: any, action: any) => {
    if (action.type === "RESET_STORE_DATA") {
      //Clear store state
      state = undefined;
    }

    return appReducer(state, action);
  };

  /* -- create persistedReducer -- */
  const persistedReducer = persistReducer(
    {
      key: "root",
      whitelist: _whitelist || [], // if you want to persist something, put it here
      storage: AsyncStorage,
    },
    rootReducer
  );

  /* -- set reducers store -- */
  if (setStoreFn) setStoreFn(store);

  /* -- replace new reducers in store -- */
  store.replaceReducer(persistedReducer as any);

  const persistor = persistStore(store);

  return { persistor };
};

export const persistor = persistStore(store);

export default store;

export const resetAllStore = () => {
  // resetAuth();
  batch(() => {
    resetUser();
    resetMe();
    resetSystem();
    resetUserGroup();
  });
};
