import Toast from "react-native-root-toast";
import SimpleToast from "react-native-simple-toast";
import { Keyboard } from "react-native";
import { interactManager } from "../utils";
import { KeyboardService } from "./KeyboardService";
import { translate } from "../global";

export const UNKNOWN_ERROR = "UNKNOWN_ERROR";

export const ErrorCodeMap: Record<string, string> = {
  "990": "error.unknown_error",
  "996": "error.empty_data",
  "997": "error.conflict_data",
  "998": "error.invalid_data",
  "999": "error.incomplete_data",
  "1001": "error.invalid_user",
  "1002": "error.invalid_authentication",
  "1003": "error.login_required",
  "1005": "error.db_error",
  "1006": "error.internal_error",
  "1020": "error.invalid_email",
  "1021": "error.invalid_password",
  "1022": "error.invalid_username",
  "1023": "error.invalid_character",
  "1024": "error.invalid_captcha",
  "PROFILE.NOT.FOUND": "error.Profile_not_found",
};

export class ToastServiceClass {
  _getRealMessage = (message: string): string => {
    if (ErrorCodeMap[message]) {
      return translate(ErrorCodeMap[message]);
    }
    if (Number.isNaN(parseInt(message))) {
      return message;
    }
    return translate("error.unknown_error");
  };

  show = (message: any, duration: "short" | "long" = "short") => {
    if (typeof message !== "string") return;
    Keyboard.dismiss();
    return interactManager(
      () => {
        Toast.show(this._getRealMessage(message), {
          duration:
            duration === "long" ? Toast.durations.LONG : Toast.durations.SHORT,
          position: Toast.positions.BOTTOM,
          shadow: false,
          opacity: 1,
        });
      },
      KeyboardService.isShown ? 300 : 0
    );
  };

  showWithGravity = (
    message: any,
    gravity: "top" | "bottom" | "center",
    duration: "short" | "long" = "short"
  ) => {
    if (typeof message !== "string") return;
    Keyboard.dismiss();
    return interactManager(
      () => {
        Toast.show(this._getRealMessage(message), {
          duration:
            duration === "long" ? Toast.durations.LONG : Toast.durations.SHORT,
          position:
            gravity == "bottom"
              ? Toast.positions.BOTTOM
              : gravity == "top"
              ? Toast.positions.TOP
              : Toast.positions.CENTER,
          shadow: false,
          opacity: 1,
        });
      },
      KeyboardService.isShown ? 300 : 0
    );
  };

  showError = (
    message: any,
    keyboardAvoid: boolean = false,
    atRoot: boolean = true,
    defaultMessage: string
  ) => {
    if (typeof message !== "string") return;
    Keyboard.dismiss();
    const finalMessage = message !== UNKNOWN_ERROR ? message : defaultMessage;
    return interactManager(() => {
      Toast.show(this._getRealMessage(finalMessage), {
        duration: Toast.durations.SHORT,
        position: Toast.positions.BOTTOM,
        shadow: false,
        opacity: 1,
      });
    }, 150);
  };

  simpleShow = (message: any, duration: "short" | "long" = "short") => {
    if (typeof message !== "string") return;
    Keyboard.dismiss();
    interactManager(() => {
      return SimpleToast.show(
        this._getRealMessage(message),
        duration === "long" ? SimpleToast.LONG : SimpleToast.SHORT
      );
    }, 150);
  };

  simpleShowWithGravity = (
    message: any,
    gravity: "top" | "bottom" | "center",
    duration: "short" | "long" = "short"
  ) => {
    if (typeof message !== "string") return;
    Keyboard.dismiss();
    interactManager(() => {
      return SimpleToast.showWithGravity(
        this._getRealMessage(message),
        duration === "long" ? SimpleToast.LONG : SimpleToast.SHORT,
        gravity === "top"
          ? SimpleToast.TOP
          : gravity === "center"
          ? SimpleToast.CENTER
          : SimpleToast.BOTTOM
      );
    }, 150);
  };

  simpleShowError = (
    message: any,
    keyboardAvoid: boolean = false,
    atRoot: boolean = true,
    defaultMessage: string
  ) => {
    if (typeof message !== "string") return;
    Keyboard.dismiss();
    const finalMessage = message !== UNKNOWN_ERROR ? message : defaultMessage;
    interactManager(() => {
      return SimpleToast.show(
        this._getRealMessage(finalMessage),
        SimpleToast.SHORT
      );
    }, 150);
  };
}

export const ToastService = new ToastServiceClass();
