import _SInfo from "react-native-sensitive-info";
import { Core } from "./Core";
import create, { StoreApi, UseBoundStore } from "zustand";
import { prevFn } from "../utils";

const KEYCHAIN = "app";
const SHARED_NAME = "app";

interface ISInfo<T> {
  data: T;
  setData: (value: ((prev: T) => T) | T) => void;
}

export class SInfo<T> {
  private readonly _defaultData: T;
  private readonly _keyName: string;
  private readonly _useStore: UseBoundStore<StoreApi<ISInfo<T>>>;
  private readonly _setData: (value: ((prev: T) => T) | T) => void;
  // init Sensitive info with store
  constructor(keyName: string, defaultData: T) {
    // create store to listen change of vars in
    this._useStore = create<ISInfo<T>>((set) => ({
      data: defaultData,
      setData: (value) => {
        set((prev) => ({
          ...prev,
          data: prevFn<T>(prev.data, value),
        }));
      },
    }));
    this._setData = this._useStore.getState().setData;
    this._keyName = keyName;
    this._defaultData = defaultData;
    this.setDefaultData(keyName).then();
  }

  private setDefaultData = async (keyName: string) => {
    const _dataString = await _SInfo.getItem(keyName, {
      sharedPreferencesName: SHARED_NAME,
      keychainService: KEYCHAIN,
      accessGroup: Core.accessGroup,
      // providerName: Core.isMasterApp !== "true" ? Core.providerName : undefined,
    });
    if (_dataString === undefined || _dataString === null) return;
    this._setData(JSON.parse(_dataString));
  };

  setItem = async (value: ((prev: T) => T) | T) => {
    return this._setData((prev: any) => {
      _SInfo.setItem(
        this._keyName,
        JSON.stringify({
          // @ts-ignore
          ...(typeof value === "function" ? value(prev) : value),
        }),
        {
          sharedPreferencesName: SHARED_NAME,
          keychainService: KEYCHAIN,
          accessGroup: Core.accessGroup,
          // providerName:
          //   Core.isMasterApp === "true" ? Core.providerName : undefined,
        }
      );
      // @ts-ignore
      return typeof value === "function" ? value(prev) : value;
    });
  };

  getItem = async (keyName?: string) => {
    const _dataString = await _SInfo.getItem(this._keyName, {
      sharedPreferencesName: SHARED_NAME,
      keychainService: KEYCHAIN,
      accessGroup: Core.accessGroup,
      // providerName: Core.isMasterApp !== "true" ? Core.providerName : undefined,
    });

    if (_dataString === undefined || _dataString === null) {
      this._setData(this._defaultData);
      return this._defaultData;
    }

    if (keyName === undefined) {
      this._setData(JSON.parse(_dataString) as T);
      return JSON.parse(_dataString) as T;
    }
    this._setData(JSON.parse(_dataString)?.[keyName]);
    return JSON.parse(_dataString)?.[keyName];
  };

  reset = async () => {
    await _SInfo.deleteItem(this._keyName, {
      sharedPreferencesName: SHARED_NAME,
      keychainService: KEYCHAIN,
      accessGroup: Core.accessGroup,
      // providerName: Core.isMasterApp === "true" ? Core.providerName : undefined,
    });
    this._setData(this._defaultData);
  };

  get useStore(): UseBoundStore<StoreApi<ISInfo<T>>> {
    return this._useStore;
  }

  get data(): T {
    return this._useStore.getState().data;
  }
}

interface IUser {
  name: string;
  email: string;
  gavatar: string;
}

interface IEmail {
  client_key: string;
  access_token: string;
  client_secret: string;
  user_id: string;
  client_id: string;
  system_id: string;
  user: IUser;
}

export interface IBasicProjectSInfo {
  currentEmail: string;
  allEmails: string[];
  byEmail: Record<string, IEmail>;
}

export const BasicProjectSInfo = new SInfo<IBasicProjectSInfo>("basicproject", {
  currentEmail: "",
  allEmails: [],
  byEmail: {},
});
