/**
 * return headings (<h2></h2>, <h1></h1>, ...)
 */
export const headingTagRex = /<\s*\/?h\d*>/g;

/**
 * return content between opening headings (<h1>, <h2>, ...)
 */
export const contentBetweenOpenTagRex = /<h\d>((?!<\/h\d>).|\n)*?<h\d>/g;

/**
 *  return content between closing headings (</h1>, </h2>, ...)
 */
export const contentBetweenCloseTagRex = /<\/h\d>((?!<h\d>).|\n)*?<\/h\d>/g;

/**
 * Normal format: 1000.23 => 1,000.23; 1000 => 1,000
 */
export const moneyRegex = /\B(?=(\d{3})+(?!\d))/g;

/**
 * return packages of <img/>
 */
const srcImgRegex = /src\s*=\s*"(.+?)"/g;

const htmlToText = /<[^>]+>/g;

// {{@}}  {{@hanh05}} {{@giangmember}}
const strBetweenCloseBracket = /{{@(.*?)}}/g;

// replace with name of content
const a = "{{@}}  {{@hanh05}} {{@giangmember}}".replace(
  /{{@(?<name>.*?)}}/g,
  "$<name>"
);
