import DVInfo from "react-native-device-info";
import { Platform } from "react-native";

class SDeviceInfo {
  private _id = "";
  private _os = "";
  private _appVersion = "";
  private _name = "";
  private _isTablet = false;
  private _systemVersion = "";
  private _buildNumber = "";

  setupDeviceInfo = async () => {
    const id = await DVInfo.getUniqueId();
    const name = await DVInfo.getDeviceName();
    const version = DVInfo.getVersion();
    this._isTablet = DVInfo.isTablet();
    this._id = id;
    this._os = Platform.OS;
    this._appVersion = version;
    this._name = name;
    this._systemVersion = DVInfo.getSystemVersion();
    this._buildNumber = DVInfo.getBuildNumber();
  };

  get id(): string {
    return this._id;
  }

  get os(): string {
    return this._os;
  }

  get appVersion(): string {
    return this._appVersion;
  }

  get name(): string {
    return this._name;
  }

  get isTablet(): boolean {
    return this._isTablet;
  }

  get systemVersion(): string {
    return this._systemVersion;
  }

  get buildNumber(): string {
    return this._buildNumber;
  }
}

export const DeviceInfo = new SDeviceInfo();
