import { ImageSourcePropType } from "react-native";

class _App {
  private _title: string = "";
  private _subTitle: string = "";
  private _primaryColor: string = "";
  private _logo: ImageSourcePropType | undefined = undefined;
  private _preloadImage: ImageSourcePropType | undefined = undefined;
  private _preSyncData: () => Promise<void> = async () => {};
  private _onLogout: () => Promise<void> = async () => {};
  private _buttonTextColor = "";

  get title(): string {
    return this._title;
  }

  set title(value: string) {
    this._title = value;
  }

  get subTitle(): string {
    return this._subTitle;
  }

  set subTitle(value: string) {
    this._subTitle = value;
  }

  get primaryColor(): string {
    return this._primaryColor;
  }

  set primaryColor(value: string) {
    this._primaryColor = value;
  }

  get preSyncData(): () => Promise<void> {
    return this._preSyncData;
  }

  set preSyncData(value: () => Promise<void>) {
    this._preSyncData = value;
  }

  get onLogout(): () => Promise<void> {
    return this._onLogout;
  }

  set onLogout(value: () => Promise<void>) {
    this._onLogout = value;
  }

  get logo(): ImageSourcePropType | undefined {
    return this._logo;
  }

  set logo(value: ImageSourcePropType | undefined) {
    this._logo = value;
  }

  get buttonTextColor(): string {
    return this._buttonTextColor;
  }

  set buttonTextColor(value: string) {
    this._buttonTextColor = value;
  }

  get preloadImage(): ImageSourcePropType | undefined {
    return this._preloadImage;
  }

  set preloadImage(value: ImageSourcePropType | undefined) {
    this._preloadImage = value;
  }

  setup(values: {
    title: string;
    subTitle: string;
    primaryColor: string;
    preSyncData: () => Promise<void>;
    onLogout: () => Promise<void>;
    logo: ImageSourcePropType;
    buttonTextColor?: string;
    preloadImage?: ImageSourcePropType;
  }) {
    this._title = values.title;
    this._subTitle = values.subTitle;
    this._primaryColor = values.primaryColor;
    this._preSyncData = values.preSyncData;
    this._onLogout = values.onLogout;
    this._logo = values.logo;
    this._preloadImage = values.preloadImage;
    this._buttonTextColor = values.buttonTextColor || "";
  }
}

export const AppInfo = new _App();
