// it's very important to check scale

import {Dimensions} from 'react-native';
import DVInfo from 'react-native-device-info';

export const isTablet = DVInfo.isTablet();

// tuy vao thiet ke de cai dat mac dinh rieng
const ORIGINAL_WIDTH = 375; // iphone X
const ORIGINAL_HEIGHT = 812; // iphone X

const {width, height} = Dimensions.get('window');

const heightPercentage = isTablet
  ? 1
  : (height > width ? height : width) / ORIGINAL_HEIGHT; // lay canh nao nho hon
const widthPercentage = isTablet
  ? 1
  : (height > width ? width : height) / ORIGINAL_WIDTH;

// height
export const getHeight = (size: number) => {
  return size * heightPercentage;
};

// width
export const getWidth = (size: number) => {
  return size * widthPercentage;
};

// font-size and line-height
export const getFontSize = (size: number) => {
  const averageChange = (heightPercentage + widthPercentage) / 2;
  return size * averageChange;
};

const [shortDimension, longDimension] =
  width < height ? [width, height] : [height, width];

export const screenShortDimension = shortDimension;
export const screenLongDimension = longDimension;
export const isLandscape = () => width > height;
