import { prevFn, prevPFn } from "./functions";
import { create } from "zustand";
import { shallow } from "zustand/shallow";
import { TPrevFn, TPrevPFn } from "../types";
import _ from "lodash";
type TSubcribe<T> = (listener: (state: T, prevState: T) => void) => () => void;
type ReturnType<T> = [
  () => T,
  () => T,
  (value: TPrevFn<T>) => void,
  TSubcribe<T>
];

export const useZustand = <T>(initValue: T): ReturnType<T> => {
  const useStore = create(() => ({ value: initValue }));

  const useValue = () => useStore((state) => state.value);
  const getValue = () => useStore.getState().value;
  const setValue = (value: TPrevFn<T>) =>
    useStore.setState((prev) => ({ value: prevFn<T>(prev.value, value) }));
  const subscribe = (listener: (state: T, prevState: T) => void) =>
    useStore.subscribe((state, prevState) =>
      listener(state.value, prevState.value)
    );

  return [useValue, getValue, setValue, subscribe];
};

export const useZustandObject = <T>(initValue: T): ReturnType<T> => {
  const useStore = create(() => initValue);

  const useValue = () => useStore();
  const getValue = () => useStore.getState();
  const setValue = (value: TPrevFn<T>) => useStore.setState(value);
  const subscribe = useStore.subscribe;

  return [useValue, getValue, setValue, subscribe];
};

/*  -- create Array store -- */
interface IStore<T, ID = string> {
  byKey: Record<string, T>;
  query: Record<string, ID[]>;
}

type IFunction<T, ID = string> = [
  (id: ID) => T | undefined,
  (id: ID) => T | undefined,
  (query: string) => ID[],
  (query: string) => ID[],
  (items: TPrevPFn<Record<string, T>, T[]>) => void,
  (query: TPrevFn<Record<string, ID[]>>) => void,
  (items: T[]) => void,
  () => void,
  () => Record<string, T>,
  () => Record<string, T>
];

export const createArrayZustand = <T, ID = string>(
  mainKey: string[]
): IFunction<T, ID> => {
  const useStore = create<IStore<T, ID>>((set) => ({
    byKey: {},
    query: {},
  }));

  const getItems = (id?: ID) => {
    return useStore.getState().byKey?.[id?.toString() || ""];
  };

  const useItems = (id?: ID, key?: keyof T) => {
    return useStore((state) => {
      if (key) return state?.byKey?.[id?.toString() || ""]?.[key] as T[keyof T];
      return state?.byKey?.[id?.toString() || ""] as T | undefined;
    }, shallow) as any;
  };

  const getItemsByQuery = (query: string) => {
    return useStore.getState().query?.[query] || [];
  };

  const useItemsByQuery = (query: string) => {
    return useStore((state) => (state.query?.[query] || []) as ID[], shallow);
  };

  const syncItems = (items: TPrevPFn<Record<string, T>, T[]>) => {
    return useStore.setState((prev) => {
      let pairs = [];

      for (let key of mainKey) {
        for (let item of prevPFn(prev.byKey, items)) {
          // @ts-ignore
          pairs.push([item[key], item]);
        }
      }

      return {
        ...prev,
        byKey: {
          ...prev.byKey,
          ..._.fromPairs(pairs),
        },
      } as IStore<T, ID>;
    });
  };

  const deleteItems = (items: T[]) => {
    return useStore.setState((prev) => {
      let pairs = [];
      for (let key of mainKey) {
        for (let item of items) {
          // @ts-ignore
          pairs.push([item[key], undefined]);
        }
      }
      return {
        ...prev,
        byKey: {
          ...prev.byKey,
          ..._.fromPairs(pairs),
        },
      };
    });
  };

  const reset = () => {
    return useStore.setState({
      byKey: {},
      query: {},
    });
  };

  const setItemsQuery = (query: TPrevFn<Record<string, ID[]>>) => {
    return useStore.setState((prev) => {
      const newQuery = prevFn(prev.query, query);
      return {
        ...prev,
        query: newQuery,
      };
    });
  };

  const use = (): Record<string, T> => {
    return useStore((state) => state?.byKey, shallow);
  };

  const get = (): Record<string, T> => {
    return useStore.getState().byKey;
  };

  return [
    useItems,
    getItems,
    useItemsByQuery,
    getItemsByQuery,
    syncItems,
    setItemsQuery,
    deleteItems,
    reset,
    use,
    get,
  ];
};
