import { Fetch } from "../../services";
import { IGiphy } from "./types";
import { batch } from "react-redux";
import { setGiphyQuery, syncGiphy } from "./index";

export const requestTrendingGiphy = async (params: {
  type: "sticker" | "gif";
}) => {
  const { data } = await Fetch.getWithToken<{
    data: IGiphy[];
  }>(`https://api.giphy.com/v1/${params.type}s/trending`, {
    api_key: "2Ae553PBFzVPXI6ZT9fcg7KbHosGJkaq",
    limit: 25,
    rating: "g",
  });

  batch(() => {
    if (data.data.length) {
      syncGiphy(data.data);
      setGiphyQuery((prev) => ({
        ...prev,
        ["trending/" + params.type]: data.data.map((item: IGiphy) => item.id),
      }));
    }
  });
};
export const requestQueryGiphy = async (param: {
  search: string;
  type: "sticker" | "gif";
}) => {
  const { data } = await Fetch.getWithToken<{
    data: IGiphy[];
  }>(`https://api.giphy.com/v1/${param.type}s/search`, {
    q: param.search,
    api_key: "2Ae553PBFzVPXI6ZT9fcg7KbHosGJkaq",
    limit: 25,
    rating: "g",
  });

  batch(() => {
    if (data.data.length) {
      syncGiphy(data.data);
      setGiphyQuery((prev) => ({
        ...prev,
        ["search/" + param.type]: data.data.map((item: IGiphy) => item.id),
      }));
    }
  });
};
