import { EImageSize } from "../types";

class ImageHelperClass {
  url: string;
  constructor(url: string) {
    this.url = url;
  }

  getImageSize = (size: EImageSize) => {
    return this.url.replace(/\/([^/]+)$/g, `/${size}$1`);
  };
}

export const ImageHelper = (url: string) => new ImageHelperClass(url);
