import {
  IC_EMOTION_ANGRY,
  IC_EMOTION_CARE,
  IC_EMOTION_HAHA,
  IC_EMOTION_LIKE,
  IC_EMOTION_LOVE,
  IC_EMOTION_SAD,
  IC_EMOTION_WOW,
} from "../assets";
import { EReaction } from "../types";

class CReaction {
  getReactionImage = (reaction: EReaction | string) => {
    return {
      like: IC_EMOTION_LIKE,
      love: IC_EMOTION_LOVE,
      haha: IC_EMOTION_HAHA,
      sad: IC_EMOTION_SAD,
      wow: IC_EMOTION_WOW,
      angry: IC_EMOTION_ANGRY,
      care: IC_EMOTION_CARE,
    }[reaction];
  };

  getReactionText = (reaction: EReaction | string) => {
    return {
      like: "Like",
      love: "Love",
      haha: "Haha",
      sad: "Sad",
      wow: "Wow",
      angry: "Angry",
      care: "Care",
    }[reaction];
  };

  getReactionColor = (reaction: EReaction | string) => {
    return {
      like: "#267CDE",
      love: "#F1465C",
      haha: "#FBD52F",
      sad: "#FBD52F",
      wow: "#FBD52F",
      angry: "#F46B33",
      care: "#FBD52F",
    }[reaction];
  };
}

export const Reaction = new CReaction();
