import { useSharedValue, useWorkletCallback } from "react-native-reanimated";
import { useMemo } from "react";
import constate from "constate";

const Context = () => {
  const animatedVisible = useSharedValue<0 | 1 | 2>(0);

  const setBottomTabVisible = useWorkletCallback(
    (value: 0 | 1 | 2) => {
      animatedVisible.value = value;
    },
    [animatedVisible]
  );

  return useMemo(
    () => ({
      animatedVisible,
      setBottomTabVisible,
    }),
    [animatedVisible, setBottomTabVisible]
  );
};

export const [BottomTabBarProvider, useBottomTabBar] = constate(
  Context,
  (val) => val
);
