import {
  runOnUI,
  SharedValue,
  useDerivedValue,
  useSharedValue,
} from "react-native-reanimated";
import { Dimensions, ScaledSize } from "react-native";
import React, {
  createContext,
  PropsWithChildren,
  useCallback,
  useContext,
  useEffect,
  useMemo,
} from "react";

interface Props {
  width: SharedValue<any>;
  height: SharedValue<any>;
  heightWindow: SharedValue<any>;
}

const Context = createContext<Props | null>(null);

export const AnimatedWindowProvider = (props: PropsWithChildren) => {
  const window = useSharedValue(Dimensions.get("window"));
  const heightWindow = useSharedValue(Dimensions.get("window").height);

  const handleWindow = useCallback(
    ({ window: _window }: { window: ScaledSize }) => {
      "worklet";
      window.value = _window;
    },
    []
  );

  useEffect(() => {
    Dimensions.addEventListener("change", runOnUI(handleWindow));
  }, []);

  const width = useDerivedValue(
    () =>
      window.value.width < window.value.height
        ? window.value.width
        : window.value.height,
    []
  );
  const height = useDerivedValue(
    () =>
      window.value.width < window.value.height
        ? heightWindow.value
        : window.value.width,
    []
  );

  const value = useMemo(
    () => ({
      width,
      height,
      heightWindow,
    }),
    []
  );

  return <Context.Provider value={value}>{props.children}</Context.Provider>;
};

export const useAnimatedWindow = () => {
  return useContext(Context) as Props;
};

