import memoizeOne from "memoize-one";
import _ from "lodash";
import { StringHelper } from "../helpers";
import { decode, encode } from "base-64";
import { Text, TextStyle } from "react-native";
import { AUDIO_EXT } from "../assets/extension/audio";
import { VIDEO_EXT } from "../assets/extension/video";
import {
  IC_DOC,
  IC_FILE,
  IC_IMG,
  IC_MP3,
  IC_MP4,
  IC_PDF,
  IC_PPT,
  IC_TXT,
  IC_XLS,
} from "../assets";

export const stripSpecialCharacters = (
  text: string = "",
  trimWhiteSpaces: boolean = false
): string => {
  return StringHelper(text || "")
    .stripHtml({ trimWhiteSpaces })
    .toString();
};

export function decodeUnicode(str: string) {
  // Going backwards: from bytestream, to percent-encoding, to original string.
  return decodeURIComponent(
    decode(str)
      .split("")
      .map(function (c: any) {
        return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
      })
      .join("")
  );
}

export function encodeUnicode(str: string) {
  // first we use encodeURIComponent to get percent-encoded UTF-8,
  // then we convert the percent encodings into raw bytes which
  // can be fed into btoa.
  return encode(
    encodeURIComponent(str).replace(
      /%([0-9A-F]{2})/g,
      function toSolidBytes(match, p1) {
        // @ts-ignore
        return String.fromCharCode("0x" + p1);
      }
    )
  );
}

export const normalizeStringForSearch = (raw: string = ""): string => {
  let str: string = raw.trim();
  str = str.toLowerCase();
  str = removeAccents(str);
  return str;
};

export const removeAccents = (str: string): string => {
  if (str.length === 0) return str;
  str = str.replace(/[àáạảãâầấậẩẫăằắặẳẵ]/g, "a");
  str = str.replace(/[ÀÁẠẢÃÂẦẤẬẨẪĂẰẮẶẲẴ]/g, "A");
  str = str.replace(/[èéẹẻẽêềếệểễ]/g, "e");
  str = str.replace(/[ÈÉẸẺẼÊỀẾỆỂỄ]/g, "E");
  str = str.replace(/[ìíịỉĩ]/g, "i");
  str = str.replace(/[ÌÍỊỈĨ]/g, "I");
  str = str.replace(/[òóọỏõôồốộổỗơờớợởỡ]/g, "o");
  str = str.replace(/[ÒÓỌỎÕÔỒỐỘỔỖƠỜỚỢỞỠ]/g, "O");
  str = str.replace(/[ùúụủũưừứựửữ]/g, "u");
  str = str.replace(/[ÙÚỤỦŨƯỪỨỰỬỮ]/g, "U");
  str = str.replace(/[ỳýỵỷỹ]/g, "y");
  str = str.replace(/[ỲÝYỶỸ]/g, "Y");
  str = str.replace(/đ/g, "d");
  str = str.replace(/Đ/g, "D");
  str = str.replace(/[!@%^*()+=<>?\/,.:;'"&#\[]~\$_`-{}|\\]/g, " ");
  str = str.replace(/ + /g, " ");
  return str;
};

export const memoizedNormalizeStringForSearch = memoizeOne(
  normalizeStringForSearch
);

export const parseQueryString = (
  queryString: string
): Record<string, string> => {
  let params = {},
    queries,
    temp,
    i,
    l;
  // Split into key/value pairs
  queries = queryString.split("&");
  // Convert the array of strings into an object
  for (i = 0, l = queries.length; i < l; i++) {
    temp = queries[i].split("=");
    // @ts-ignore
    params[temp[0]] = temp[1];
  }
  return params;
};

// noinspection SuspiciousTypeOfGuard
export const decodeHtml = (str: string) =>
  require("he").decode(typeof str === "string" ? str : "");

export const decodeHtmlMemoized = _.memoize(decodeHtml);

export const setCustomText = (customProps: { style: TextStyle }) => {
  // @ts-ignore
  const TextRender = Text.render;
  // @ts-ignore
  const initialDefaultProps = Text.defaultProps;
  // @ts-ignore
  Text.defaultProps = {
    ...initialDefaultProps,
    ...customProps,
  };
  // @ts-ignore
  Text.render = function render(props, ref) {
    let oldProps = props;
    // @ts-ignore
    props = { ...props, style: [customProps.style || {}, props.style] };
    try {
      return TextRender.call(this, props, ref);
    } finally {
      props = oldProps;
    }
  };
};

const moneyRegex = /\B(?=(\d{3})+(?!\d))/g;
//Normal format: 1000.23 => 1,000.23; 1000 => 1,000
export const formatFloat = (value: string | number | undefined) => {
  try {
    if (typeof value === undefined) {
      return "0";
    }
    const dataValueSplit = String(value).split(".");
    if (dataValueSplit.length == 1) {
      return String(value).replace(moneyRegex, ",");
    } else {
      return (
        String(dataValueSplit[0]).replace(moneyRegex, ",") +
        "." +
        dataValueSplit[1]
      );
    }
  } catch (error) {
    return value;
  }
};

export const getIcFile = (name: string) => {
  const arr = name.split(".");
  const type = arr[arr.length - 1];

  if (type == "pdf") return IC_PDF;
  if (type.indexOf("doc") !== -1) return IC_DOC;
  if (type == "png") return IC_IMG;
  if (type == "txt") return IC_TXT;
  if (type.indexOf("ppt") !== -1) return IC_PPT;
  if (type.indexOf("xls") !== -1) return IC_XLS;
  if (AUDIO_EXT.indexOf(type) !== -1) return IC_MP3;
  if (VIDEO_EXT.indexOf(type) !== -1) return IC_MP4;
  return IC_FILE;
};

String.prototype.replaceBetween = function (start, end, content) {
  return this.substring(0, start) + content + this.substring(end);
};

// @ts-ignore
String.prototype.replaceAsync = function (searchValue, replacer) {
  var string = this.toString();
  try {
    if (typeof replacer === "function") {
      // 1. Run fake pass of `replace`, collect values from `replacer` calls
      // 2. Resolve them with `Promise.all`
      // 3. Run `replace` with resolved values
      var values: any[] = [];
      String.prototype.replace.call(string, searchValue, function () {
        values.push(replacer.apply(undefined, arguments));
        return "";
      });
      return Promise.all(values).then(function (resolvedValues) {
        return String.prototype.replace.call(string, searchValue, function () {
          return resolvedValues.shift();
        });
      });
    } else {
      return Promise.resolve(
        String.prototype.replace.call(string, searchValue, replacer)
      );
    }
  } catch (error) {
    return Promise.reject(error);
  }
};
