import { ImageHelper } from "../helpers";
import { EImageSize, ELanguage, ESystemColor, IUser } from "../types";
import { ImageURISource } from "react-native";
import { StringHelper } from "../helpers";

const searchKeywords: Record<string, string> = {};

class UserClass {
  _raw: IUser;

  constructor(raw: IUser) {
    this._raw = raw;
  }

  getGavatar = (size: EImageSize = EImageSize.SIZE_1) => {
    return ImageHelper(this._raw.gavatar).getImageSize(size);
  };

  getDescription = () => {
    return [this._raw.title, this._raw.phone, this._raw.email]
      .filter(Boolean)
      .join(" · ");
  };

  get searchKeyword() {
    if (!searchKeywords[this._raw.id]) {
      const { username, name, email, title, phone } = this._raw;

      const { newName, newTitle } = {
        newName: StringHelper(name).removeAccents().toString(),
        newTitle: StringHelper(title).removeAccents().toString(),
      };

      searchKeywords[this._raw.id] =
        `${username} ${newName} ${email} ${newTitle} ${phone}`.toLowerCase();
    }

    return searchKeywords[this._raw.id];
  }

  /**
   * For searching
   */
  isMatch = (keyword: string) => {
    const keywordWithoutAccents = StringHelper(keyword)
      .removeAccents()
      .toLowerCase();

    return this.searchKeyword.includes(keywordWithoutAccents);
  };
}

export const User = (raw: IUser) => new UserClass(raw);

User.getGavatarImageSource = (
  user?: IUser,
  size: EImageSize = EImageSize.SIZE_1
): ImageURISource => {
  if (user) return { uri: User(user).getGavatar(size) };

  return require("../assets/images/im_default_avatar.jpg");
};

