import React, {PropsWithChildren} from 'react';
import Animated, {
  FadeIn,
  FadeOut,
  LayoutAnimation,
  LayoutAnimationsValues,
  SequencedTransition,
  withDelay,
  withSpring,
  withTiming,
} from 'react-native-reanimated';
import {ViewProps} from 'react-native';

export const AnimatedView = (
  props: Animated.AnimateProps<ViewProps> & PropsWithChildren,
) => {
  return (
    <Animated.View
      layout={SequencedTransition}
      entering={FadeIn}
      exiting={FadeOut}
      {...props}>
      {props.children}
    </Animated.View>
  );
};

export const CustomLayoutTransition: (
  targetValues: LayoutAnimationsValues,
) => LayoutAnimation = values => {
  'worklet';
  return {
    animations: {
      originX: withTiming(values.targetOriginX, {duration: 1000}),
      originY: withDelay(
        1000,
        withTiming(values.targetOriginY, {duration: 1000}),
      ),
      maxWidth: withSpring(values.targetWidth),
      maxHeight: withSpring(values.targetHeight),
    },
    initialValues: {
      originX: values.currentOriginX,
      originY: values.currentOriginY,
      maxWidth: values.currentWidth,
      maxHeight: values.currentHeight,
    },
  };
};
