import { useMe } from "../me";
import { IUser } from "./types";
import { createArrayReducer } from "../../utils";
import { useMemo } from "react";
import { User } from "../../services";

export const {
  sync: syncUser,
  reducer: userReducer,
  setStore: setUserStore,
  useByKey: useUser,
  setQueries: setUserQueries,
  useKeysByQuery: useUserByQuery,
  getKeysByQuery: getUserByQuery,
  getByKey: getUser,
  deleteItem: deleteUser,
  reset: resetUser,
} = createArrayReducer<IUser>("users", ["id", "username"]);

type Options = {
  userIds?: string[];
  includingMe?: boolean;
};

export const useUsersByKeyword = (keyword: string, options?: Options) => {
  const usersByQuery = useUserByQuery("allIds");
  const users = useMemo(
    () => usersByQuery.map((id) => getUser(id)),
    [usersByQuery]
  );
  const me = useMe();

  return useMemo(() => {
    const newOptions = {
      userIds: undefined,
      includingMe: true,
      ...options,
    };
    const userIds = new Set(newOptions.userIds || users.map((u) => u?.id)); // if not set, then set all users

    if (me && !newOptions.includingMe) userIds.delete(me.id);

    if (!keyword)
      return users.filter((u) => userIds.has(u?.id || "")) as IUser[];

    return users
      .map((user) =>
        user && userIds.has(user?.id || "") && User(user).isMatch(keyword)
          ? user
          : false
      )
      .filter(Boolean) as IUser[];
  }, [users, keyword, options]);
};

export * from "./types";
