// @ts-ignore does not provide TypeScript definitions
import load from "@loadable/component";
import React, { Suspense } from "react";
import { styled } from "../global";

export const lazily = <T extends {}, U extends keyof T>(
  loader: (x?: string) => Promise<T>,
  fallback?: JSX.Element
) =>
  new Proxy({} as unknown as T, {
    get: (target, componentName: string | symbol) => {
      if (typeof componentName === "string") {
        const C = React.lazy(() =>
          loader(componentName).then((x) => ({
            default: x[componentName as U] as any as React.ComponentType<any>,
          }))
        );

        return (props: any) => (
          <Suspense
            fallback={
              fallback ? (
                fallback
              ) : (
                <SView>
                  <SActivityIndicator />
                </SView>
              )
            }
          >
            <C {...props} />
          </Suspense>
        );
      }
    },
  });

// see https://loadable-components.com/docs/babel-plugin/#loadable-detection
export const loadable = <T extends {}, U extends keyof T>(
  loader: () => Promise<T>,
  opts?: any
) =>
  new Proxy({} as unknown as T, {
    // @ts-ignore
    get: (target: T, componentName: U) => {
      if (typeof componentName === "string") {
        return load(() => loader().then((x: T) => x[componentName]), opts);
      }
    },
  });

const SView = styled.View`
  flex: 1;
  background-color: ${(p) => p.theme.backgroundColor};
  align-items: center;
  justify-content: center;
`;

const SActivityIndicator = styled.ActivityIndicator.attrs((p) => ({
  color: p.theme.neutral2,
}))``;
