import { useSharedValue } from "react-native-reanimated";
import { useCallback, useEffect, useMemo } from "react";
import constate from "constate";
import { MaxKeyboard } from "./store";
import {
  KeyboardEvents,
  useKeyboardHandler,
} from "react-native-keyboard-controller";

const _useAnimatedKeyboard = () => {
  const keyboardState = useSharedValue(0);
  const keyboardHeight = useSharedValue(0);
  const maxKeyboardHeight = useSharedValue(0);
  const extendedKeyboardHeight = useSharedValue(0);

  /* -- get max keyboard height from db -- */
  useEffect(() => {
    (async () => {
      const height = await MaxKeyboard.getHeight();

      maxKeyboardHeight.value = height;
      extendedKeyboardHeight.value = height;
    })();
  }, []);

  const onKeyboardShowHandler = useCallback((e: { height: number }) => {
    extendedKeyboardHeight.value = e.height;

    if (maxKeyboardHeight.value < e.height) {
      maxKeyboardHeight.value = e.height;
    }
  }, []);

  useEffect(() => {
    let show = KeyboardEvents.addListener(
      "keyboardWillShow",
      onKeyboardShowHandler
    );
    return () => show.remove();
  }, []);

  useKeyboardHandler({
    onStart: (e) => {
      "worklet";
      keyboardState.value = e.progress;
      keyboardHeight.value = e.height;
    },
  });

  return useMemo(
    () => ({
      keyboardHeight,
      keyboardState,
      maxKeyboardHeight,
      extendedKeyboardHeight,
    }),
    [keyboardHeight, keyboardState, maxKeyboardHeight, extendedKeyboardHeight]
  );
};

export const [AnimatedKeyboardProvider, useAnimatedKeyboard] = constate(
  _useAnimatedKeyboard,
  (value) => value
);

export * from "./store";
