import DocumentPicker, {
  DocumentPickerOptions,
  DocumentPickerResponse,
} from "react-native-document-picker";
import { Alert, Linking, PermissionsAndroid, Platform } from "react-native";
import Share from "react-native-share";
import { Buffer } from "buffer";
import mime from "mime";
import axios from "axios";
import { CameraRoll } from "@react-native-camera-roll/camera-roll";
import RNFetchBlobUtil from "rn-fetch-blob";
import ImagePicker, {
  Image as ImagePickerImage,
  Options as ImagePickerOptions,
} from "react-native-image-crop-picker";
import {
  PERMISSIONS,
  request,
  requestMultiple,
} from "react-native-permissions";
import ImageResizer from "react-native-image-resizer";
import DeviceInfo from "react-native-device-info";
import {
  EXTENSION,
  IC_DOC,
  IC_FILE,
  IC_IMG,
  IC_MP3,
  IC_MP4,
  IC_PDF,
  IC_PPT,
  IC_TXT,
  IC_XLS,
} from "../assets";

export type FileType = Partial<Omit<ImagePickerImage, "size">> &
  Omit<DocumentPickerResponse, "fileCopyUri" | "size"> & {
    duration?: number;
    size?: number;
  } & object;

type DownloadOptions = {
  url: string;
  filename?: string;
  openFile?: boolean;
  isImage?: boolean;
};

export type TakeCameraOptions = ImagePickerOptions;
export type PickImageOptions = ImagePickerOptions;
export type PickFileOptions = {
  multiple?: boolean;
};
export type ProgressCallback = (progress: number, total: number) => any;

const isAndroid11 = Platform.OS == "android" && Platform.Version >= 33;

class SFile {
  pick = async ({
    multiple,
    ...options
  }: PickFileOptions &
    DocumentPickerOptions<"ios" | "android" | "windows">) => {
    await this.askReadPermission();

    if (multiple) {
      // @ts-ignore
      return await DocumentPicker.pickMultiple({
        type: [DocumentPicker.types.allFiles],
        ...options,
      });
    }
    // @ts-ignore
    const res = await DocumentPicker.pick({
      type: [DocumentPicker.types.allFiles],
      ...options,
    });
    if (Array.isArray(res)) {
      return res;
    } else {
      if (res) {
        return [res];
      }
      return [];
    }
  };

  askWritePermission = async () => {
    if (Platform.OS !== "android" || isAndroid11) return;
    const response = await PermissionsAndroid.request(
      PermissionsAndroid.PERMISSIONS.WRITE_EXTERNAL_STORAGE
    );

    if (response !== "granted") throw new Error("PERMISSION_REQUEST_FAILED");

    return response;
  };

  askReadPermission = async () => {
    if (Platform.OS !== "android") return;
    const response = isAndroid11
      ? await requestMultiple([
          PERMISSIONS.ANDROID.READ_MEDIA_IMAGES,
          PERMISSIONS.ANDROID.READ_MEDIA_VIDEO,
          PERMISSIONS.ANDROID.READ_MEDIA_AUDIO,
        ]).then((res: any) => {
          for (let key in res) {
            if (res[key] !== "granted") {
              return res[key];
            }
          }
          return "granted";
        })
      : await request(PERMISSIONS.ANDROID.READ_EXTERNAL_STORAGE);

    if (response !== "granted") throw new Error("PERMISSION_REQUEST_FAILED");

    return response;
  };

  private _extractFileNameFromPath = (path: string) => {
    const splitFromPath = /\w+.\w+$/.exec(path);
    return splitFromPath && splitFromPath[0]
      ? splitFromPath[0]
      : Math.random().toString(36) + ".png";
  };

  takeCamera = async (options: TakeCameraOptions): Promise<FileType[]> => {
    if (Platform.OS === "android") {
      const status = await PermissionsAndroid.request(
        PermissionsAndroid.PERMISSIONS.CAMERA
      );

      if (status !== "granted") {
        throw new Error("PERMISSION_REQUEST_FAILED");
      }
    }

    const result = await ImagePicker.openCamera(options);

    const output: FileType[] = [];

    if (Array.isArray(result)) {
      result.forEach((image) => {
        const i: FileType = {
          ...image,
          name: image.filename || this._extractFileNameFromPath(image.path),
          // @ts-ignore
          size: image.size + "",
          type: image.mime || "*/*",
          uri: image.path,
        };

        output.push(i);
      });
    } else {
      // @ts-ignore
      const i: FileType = {
        ...result,
        name: result.filename || this._extractFileNameFromPath(result.path),
        size: result.size,
        type: result.mime || "*/*",
        uri: result.path,
      };

      output.push(i);
    }

    return output;
  };

  pickImage = async (
    options: PickImageOptions & {
      changeFileNameExtensionFromHEICToJpg?: boolean;
      onPermissionLimited?: () => any;
      resizeOptions?: {
        quality?: number;
        width?: number;
      };
    }
  ): Promise<FileType[]> => {
    const permissionResponse = isAndroid11
      ? await requestMultiple([
          PERMISSIONS.ANDROID.READ_MEDIA_IMAGES,
          PERMISSIONS.ANDROID.READ_MEDIA_VIDEO,
        ]).then((res: any) => {
          for (let key in res) {
            if (res[key] !== "granted") {
              return res[key];
            }
          }
          return "granted";
        })
      : await request(
          Platform.select({
            ios: PERMISSIONS.IOS.PHOTO_LIBRARY,
            default: PERMISSIONS.ANDROID.READ_EXTERNAL_STORAGE,
          })
        );

    if (permissionResponse !== "granted") {
      if (permissionResponse === "limited" && options.onPermissionLimited) {
        options.onPermissionLimited();
      }
    }

    const pick = await ImagePicker.openPicker(options);

    let output: FileType[] = [];

    const renameImage = async (image: any) => {
      const strSplit = (image.filename || "").split(".");
      if (strSplit?.length > 1 && strSplit[1] === "HEIC") {
        image.filename = strSplit[0] + ".jpg";
        image.name = strSplit[0] + ".jpg";
      }
      const width = options?.resizeOptions?.width;
      const quality = options?.resizeOptions?.quality;
      return await ImageResizer.createResizedImage(
        image.uri || image.path,
        width || 1440,
        width || 1440,
        "JPEG",
        quality || 75
      );
    };

    if (Array.isArray(pick)) {
      for (let i = 0; i < pick.length; i++) {
        // @ts-ignore
        const image = pick[i];
        if (!image) {
          continue;
        }
        let file: FileType = {
          ...image,
          uri: image.path,
          type: image.mime || "*/*",
          size: image.size + "",
          name: image.filename || this._extractFileNameFromPath(image.path),
        };

        if (options.mediaType == "video" || file?.type?.includes("video")) {
          output.push(file);
        } else {
          const newImage = await renameImage(file);
          // const base64 = await this.toBase64(newImage.path);
          output.push({
            ...newImage,
            type: image.mime || "*/*",
            // data: base64,
          } as FileType);
        }
      }
    } else {
      let file = {
        ...pick,
        uri: pick.path,
        type: pick.mime || "*/*",
        size: pick.size,
        name: pick.filename || this._extractFileNameFromPath(pick.path),
      };

      if (options.mediaType == "video" || file?.type?.includes("video")) {
        output.push(file as FileType);
      } else {
        const newImage = await renameImage(file);
        // const base64 = await this.toBase64(newImage.path);
        output.push({
          ...newImage,
          // data: base64,
          type: pick.mime || "*/*",
        } as FileType);
      }
    }
    return output;
  };

  /**
   * @todo: Support Video here
   * @param options
   */
  download = async (options: DownloadOptions) => {
    const { config, fs, android } = RNFetchBlobUtil;
    await this.askWritePermission();

    const mimeType = mime.getType(options.filename || options.url) || "";

    if (Platform.OS === "ios") {
      if (mimeType.startsWith("image")) {
        return await CameraRoll.saveToCameraRoll(options.url);
      }

      return Linking.openURL(options.url);
    }

    const filename = options.filename || /[\w.]+$/.exec(options.url);

    const download = await config({
      fileCache: true,
      addAndroidDownloads: {
        useDownloadManager: true,
        notification: true,
        path: fs.dirs.DownloadDir + "/" + filename,
        mediaScannable: true,
      },
      overwrite: true,
    }).fetch("GET", options.url);

    if (options.openFile && Platform.OS === "android")
      android.actionViewIntent(download.path(), mimeType);
    if (Platform.OS === "android")
      await fs.scanFile([{ path: download.path(), mime: mimeType }]);

    return download;
  };

  /**
   * @see https://github.com/react-native-community/react-native-share#openoptions
   *
   * But I will handle the base64 encode for you
   */
  share = async (options: { urls?: string[]; url?: string }) => {
    const getBase64 = async (url: string) => {
      if (url.startsWith("file")) {
        return url;
      }
      const response = await axios.get(url, {
        responseType: "arraybuffer",
      });
      const base64 = new Buffer(response.data, "binary").toString("base64");
      const mimeType = mime.getType(url);
      return `data:${mimeType};base64,${base64}`;
    };

    if (options.url) {
      options.url = await getBase64(options.url);
    }

    if (options.urls) {
      options.urls = await Promise.all(
        options.urls.map((url) => getBase64(url))
      );
    }

    return await Share.open(options);
  };

  getMime = ({ uri }: { uri: string }) => {
    return mime.getType(uri);
  };

  getExtensionFromMime = (mimeType: string) => {
    return mime.getExtension(mimeType);
  };

  extractFileNameFromUri = ({
    uri,
    removeQueryString = false,
  }: {
    uri: string;
    removeQueryString?: boolean;
  }) => {
    // Remove all content before last /
    let output = uri.substr(uri.lastIndexOf("/") + 1);

    if (removeQueryString) output = output.replace(/([?#].*)$/, "");

    return output;
  };

  extractExtensionFromFilename = ({ filename }: { filename: string }) => {
    return filename.replace(/^.*\./, "");
  };

  guessUriInfo = ({
    uri,
    removeQueryString = false,
  }: {
    uri: string;
    removeQueryString?: boolean;
  }) => {
    const filename = this.extractFileNameFromUri({ uri, removeQueryString });
    const extension = this.extractExtensionFromFilename({ filename });
    const mimeType = this.getMime({ uri: filename }) || "*/*";

    return {
      filename,
      extension,
      mimeType,
    };
  };

  viewFile = async ({
    uri,
    progressCallback,
    openStoreIfNotSupported = true,
  }: {
    uri: string;
    progressCallback?: ProgressCallback;
    openStoreIfNotSupported?: boolean;
  }) => {
    const { config, android } = RNFetchBlobUtil;

    if (Platform.OS === "ios") {
      await Linking.openURL(uri);
      return;
    }

    let { extension, mimeType } = this.guessUriInfo({ uri });

    let localUri: string | null = null;

    if (uri.startsWith("file") || uri.startsWith("content")) {
      localUri = uri;
    } else {
      const prepared = config({
        fileCache: true,
        overwrite: true,
        appendExt: extension,
      }).fetch("GET", uri);

      if (progressCallback) {
        prepared.progress(progressCallback);
      }

      const download = await prepared;
      localUri = download.path();
      if (typeof download.respInfo.headers === "object") {
        const {
          ["content-type"]: contentType1,
          ["Content-Type"]: contentType2,
        } = download.respInfo.headers as Record<string, any>;
        if (contentType1 || contentType2) {
          mimeType = (contentType1 || contentType2) as string;
        }
      }
    }
    if (!mimeType) {
      mimeType = this.getMime({ uri: localUri }) || "";
    }

    try {
      await android.actionViewIntent(localUri, mimeType || "*/*");
    } catch (e: any) {
      if (e.message === "NO_ACTIVITY" && openStoreIfNotSupported) {
        return Alert.alert("file_not_supported", "download_supported_app", [
          { text: "cancel", style: "cancel" },
          {
            text: "open_store",
            onPress: () => {
              return Linking.openURL(
                `https://play.google.com/store/search?q=${extension}%20viewer`
              );
            },
          },
        ]);
      }
      throw new Error("FILE_NOT_SUPPORTED");
    }
  };

  toBase64 = async (url: string) => {
    if (url.startsWith("file")) {
      return url;
    }
    try {
      const base64 = await RNFetchBlobUtil.fs.readFile(url, "base64");
      const mimeType = mime.getType(url);
      return `data:${mimeType};base64,${base64}`;
    } catch (e) {
      console.log("ERROR", e);
    }
  };

  shareUrl = (url: string, title: string) => {
    return Share.open({
      title: title,
      url: url,
      subject: title,
    });
  };

  getImageFile = ({ url, ext }: { ext?: string; url?: string }) => {
    const size = url?.split(".");
    let _ext = "";
    if (size) _ext = size[size.length - 1];
    if (ext) _ext = ext;

    if (EXTENSION.VIDEO.indexOf(_ext) !== -1) return IC_MP4;
    if (_ext.indexOf("txt") !== -1) return IC_TXT;
    if (EXTENSION.EXCEL.indexOf(_ext) !== -1) return IC_XLS;
    if (EXTENSION.WORD.indexOf(_ext) !== -1) return IC_DOC;
    if (EXTENSION.IMAGE.indexOf(_ext) !== -1) return IC_IMG;
    if (_ext.indexOf("pdf") !== -1) return IC_PDF;
    if (EXTENSION.PPT.indexOf(_ext) !== -1) return IC_PPT;
    if (EXTENSION.AUDIO.indexOf(_ext) !== -1) return IC_MP3;
    return IC_FILE;
  };
}

export const File = new SFile();
