import {
  SharedValue,
  useSharedValue,
  withDelay,
  withTiming,
} from "react-native-reanimated";
import { useCallback } from "react";

export const useAnimatedPause = (
  initialValue: 0 | 1 = 1,
  wait: number = 300
) => {
  const active = useSharedValue(initialValue);

  const onPause = useCallback(() => {
    "worklet";
    active.value = 0;
    active.value = withDelay(wait, withTiming(1, { duration: 0 }));
  }, []);

  return [active, onPause] as [SharedValue<0 | 1>, () => void];
};
/*
* Usage:
*
* const [active, pause] = useAnimatedPause()
*
* const onPress = () => {
*   if (active) {
*     your executes
*   }
*   pause()
* }
*
* */
