import { batch } from "react-redux";
import { ILogin, syncMe, setUserQueries, syncUser } from "../index";
import { syncSystem } from "../system";
import {
  getUserGroup,
  setUserGroupQueries,
  syncUserGroup,
} from "../userGroups";
import { Core } from "../../services";

export const authBatch = (data: ILogin) =>
  batch(() => {
    if (data?.viewer) syncMe(data.viewer);
    if (data.system) {
      Core.systemPath = data.system.path;
      syncSystem(data.system);
    }
    if (data.user_groups) {
      syncUserGroup(data.user_groups);
      //@ts-ignore
      setUserGroupQueries({
        allIds: data.user_groups.map((item) => item?.id),
        teams: data.user_groups
          .filter((item) => item.metatype !== "user")
          .map((item) => item?.id),
      });
    }
    // set people to redux
    if (data?.people.length) {
      syncUser(data.people);

      let userGroups: { [id: string]: string[] } = {};
      let users: string[] = [];
      let guests: string[] = [];
      for (let user of data?.people || []) {
        /* user - group */
        for (let groupId of user?.user_groups || []) {
          const userGroup = getUserGroup(groupId);
          if (userGroup?.metatype !== "user") {
            const key = "userGroup/" + groupId;
            userGroups[key] = [...(userGroups?.[key] || []), user.id];
          }
        }

        /* get company users */
        if (user?.metatype !== "guest") {
          users = [...users, user.id];
          continue;
        }

        /* get guests */
        if (user?.metatype === "guest") {
          guests = [...guests, user.id];
        }
      }

      // @ts-ignore
      setUserQueries({
        allIds: data.people.map((item) => item?.id),
        allUsernames: data.people.map((item) => item.username),
        userIds: users,
        guestIds: guests,
        ...userGroups,
      });
    }
  });
